html {
  font-family: Roboto, Arial, sans-serif;
}

body {
  --link-color-active: #00acdc;
  --link-color: #000;
  margin: 0 auto;
}

.header {
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
  padding: 0 50px;
  display: flex;
  position: relative;
}

.logo {
  width: 40px;
  height: 40px;
}

.nav ul {
  justify-content: space-between;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

li {
  margin-left: 20px;
}

li:first-child {
  margin: 0;
}

.nav__link {
  text-transform: uppercase;
  color: var(--link-color);
  text-align: center;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  display: block;
}

.nav__link:hover, .is-active {
  color: var(--link-color-active);
}

.is-active {
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--link-color-active);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

